$label-color: rgba(28, 1, 255, 1);
@mixin clearfix() {
  &::after {
    display: block;
    content: "";
    clear: both;
  }
}

.test_class{
  width: 60%;
  padding: 40px;
  border-radius: 5px;
  background: white;
  .form_froup{
    margin-bottom: 10px;
    select{
      width: 100%;
    }
  }
  .uploader{
    margin-top: 10px;
    margin-bottom: 10px;
    input{
      border: 1px solid #5b9dd9;
      border-radius: 5px;
      padding: 10px;
    }
  }
  button{
    border-width: 0;
    background-color: #00a0d2;
    border-radius: 5px;
    padding: 10px 20px;
    color: white;
    cursor: pointer;
  }
}