$label-color: rgba(28, 1, 255, 1);
@mixin clearfix() {
  &::after {
    display: block;
    content: "";
    clear: both;
  }
}

.active{
  color: red;
}
.wrap{

  ul{
    margin: 0;
    li{
      display:inline-block;
      margin: 6px;
      margin-bottom: 0;
      margin-left: 0;
      a{
        text-decoration: none;
        padding: 20px;
        line-height: 0;
        border-radius: 5px 5px 0px 0px;
        display: inline-block;
        background-color: white;
      }
  }
  }
}
